window.addEventListener('DOMContentLoaded', () => {
    animate()
    window.addEventListener('resize', () => {
        animate()
    })
})

function animate() {
    const tlLines = gsap.timeline({
        scrollTrigger: {
            trigger: '.who__nums',
            start: 'top 40%',
            end: 'bottom 80%',
            scrub: 2
        }
    })
    tlLines.to('.nums', {
        xPercent: -100
    })


    gsap.from('.cool__tag', {
        opacity: 0,
        y: 30,
        stagger: 0.2,
        duration: .05,
        scrollTrigger: {
            trigger: '.cool__tags',
            start: 'top bottom',
        }

    })
}
